import Vue from 'vue';
import Router from 'vue-router';
// Components used in routes
import Home from './views/Home.vue';
import About from './views/About.vue';
import Contact from './views/Contact.vue';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
    },
    {
      path: '/about',
      name: 'about',
      component: About,
    },
    {
      path: '/contact/:id',
      name: 'contact',
      component: Contact,
    },
  ],
});
